const API_BASE = process.env.NEXT_PUBLIC_API_URL || "http://localhost:5000/api"

export const api = {
  // Auth
  auth: {
    signup: (email: string, password: string, role: string) =>
      fetch(`${API_BASE}/auth/signup`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password, role }),
      }).then(async (r) => {
        if (!r.ok) {
          const text = await r.text();
          try {
            const json = JSON.parse(text);
            throw json;
          } catch {
            // not JSON, send string message
            throw { message: text || `Request failed with status ${r.status}` };
          }
        }


        return r.json();
      }),

    login: (email: string, password: string) =>
      fetch(`${API_BASE}/auth/login`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password }),
      }).then(async (r) => {
        if (!r.ok) {
          const text = await r.text();
          try {
            const json = JSON.parse(text);
            throw json;
          } catch {
            // not JSON, send string message
            throw { message: text || `Request failed with status ${r.status}` };
          }
        }


        return r.json();
      }),
  },

  // Players
  players: {
    create: (name: string, clubName: string) =>
      fetch(`${API_BASE}/players`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ name, clubName }),
      }).then((r) => r.json()),

    list: () => fetch(`${API_BASE}/players`).then((r) => r.json()),

    byEvent: (eventId: string) => fetch(`${API_BASE}/players/event/${eventId}`).then((r) => r.json()),
  },

  // Events
  events: {
    create: (data: any, token?: string) =>
      fetch(`${API_BASE}/events`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          ...(token ? { Authorization: `Bearer ${token}` } : {})
        },
        body: JSON.stringify(data),
      }).then(async (r) => {
        if (!r.ok) {
          const error = await r.json();
          throw error;
        }
        return r.json();
      }),

    list: () => fetch(`${API_BASE}/events`).then((r) => r.json()),

    get: (id: string) => fetch(`${API_BASE}/events/${id}`).then((r) => r.json()),

    getMatches: (eventId: string) => fetch(`${API_BASE}/events/${eventId}/matches`).then((r) => r.json()),

    generateFixtures: (eventId: string, token: string) =>
      fetch(`${API_BASE}/events/${eventId}/generate-fixtures`, {
        method: "POST",
        headers: { Authorization: `Bearer ${token}` },
      }).then((r) => r.json()),

    schedule: (eventId: string, token: string) =>
      fetch(`${API_BASE}/events/${eventId}/schedule`, {
        method: "POST",
        headers: { Authorization: `Bearer ${token}` },
      }).then((r) => r.json()),
  },

  // Matches
  matches: {
    generateCode: (matchId: string, token: string) =>
      fetch(`${API_BASE}/matches/${matchId}/generate-code`, {
        method: "POST",
        headers: { Authorization: `Bearer ${token}` },
      }).then((r) => r.json()),

    verifyCode: (matchId: string, code: string) =>
      fetch(`${API_BASE}/matches/${matchId}/verify-code`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ code }),
      }).then((r) => r.json()),

    submitScore: (matchId: string, winnerId: string, token: string) =>
      fetch(`${API_BASE}/matches/${matchId}/submit-score`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({ winnerId }),
      }).then((r) => r.json()),
  },
}
